	module.exports = {"data":{"post":{"html":"<p>A poster advertising <a href=\"http://www.axe.de/\">'anti-hangover' shower gel</a> is gracing the city this week. That alone is weird enough to warrant a post, but even stranger is the tagline (with apologies to <a title=\"Code The Web Socket\" href=\"http://ahawkins.org/weblog.php\">Alwin</a>): Need help in the morning? The nurse waits in your shower! </p>\n<p><img alt=\"axe.jpg\" src=\"http://www.papascott.de/wordpress/wp-content/uploads/2003/07/img367.jpg\" width=\"193\" height=\"289\" border=\"0\" /></p>\n<p>Somehow, this makes me think of Psycho....</p>","fields":{"title":"Waiting in the Shower","date":"08 Jul 2003","url":"https://papascott.de/2003-07-08-waiting-in-the-shower/"}}},"pathContext":{"slug":"/2003-07-08-waiting-in-the-shower/","prev":{"fields":{"title":"Stupid White Men and Women","date":"08 Jul 2003","slug":"/2003-07-08-stupid-white-men-and-women/"}},"next":{"fields":{"title":"Beyond T9","date":"09 Jul 2003","slug":"/2003-07-09-beyond-t9/"}}}}

